@import url("https://fonts.googleapis.com/css2?family=Chango&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main {
  min-width: 320px;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #83a4d4;
  background: linear-gradient(to right, #b6fbff, #83a4d4);
}
