.uploader {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .title {
    width: 75%;
    font-size: 20px;
    text-align: center;
    font-family: "Chango", cursive;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  #image1 {
    width: 800px;
    height: auto;
  }
  input[type="file"] {
    background-color: red;
    position: absolute;
    height: 100%;
    width: 100%;
    opacity: 0;
    top: 0;
    left: 0;
  }
  #preview {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      user-select: none;
      max-width: 100%;
      max-height: 100%;
    }
    .controls {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      bottom: -80px;
      position: absolute;
      font-family: "Chango", cursive;
      color: #fff;
      .slider {
        position: relative;
        border-radius: 8px;
        width: 100%;
        height: 18px;
        margin-right: 30px;
        output {
          position: absolute;
          top: -45px;
          padding: 7px 10px;
          background: #76de76;
          color: #fff;
          border-radius: 10px 10px 10px 0;
          font-weight: bold;
        }
        #fader {
          cursor: pointer;
          position: relative;
          left: 3px;
          width: 100%;
          height: 10px;
          background: #76de76;
          -webkit-appearance: none;
          outline: none;
          border-radius: 4px;
          &::-webkit-slider-thumb {
            -webkit-appearance: none;
            width: 25px;
            height: 25px;
            border-radius: 50%;
            background: #76de76;
            box-shadow: 0 0 2px #222;
          }
        }
      }
      & > img {
        user-select: none;
        cursor: pointer;
        filter: invert(100%);
        background: rgba(255, 255, 255, 0.33);
        max-width: 40px;
        border-radius: 15px;
        border: 1px solid #000;
        padding: 7.5px;
      }
    }
  }
}

@import "withAnimatedBorder";
