@import url(https://fonts.googleapis.com/css2?family=Chango&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.main{min-width:320px;width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background:#83a4d4;background:linear-gradient(to right, #b6fbff, #83a4d4)}

.uploader{position:relative;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.uploader .title{width:75%;font-size:20px;text-align:center;font-family:"Chango", cursive;color:#fff;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.uploader #image1{width:800px;height:auto}.uploader input[type="file"]{background-color:red;position:absolute;height:100%;width:100%;opacity:0;top:0;left:0}.uploader #preview{position:relative;width:100%;height:100%;text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.uploader #preview img{-webkit-user-select:none;-ms-user-select:none;user-select:none;max-width:100%;max-height:100%}.uploader #preview .controls{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;bottom:-80px;position:absolute;font-family:"Chango", cursive;color:#fff}.uploader #preview .controls .slider{position:relative;border-radius:8px;width:100%;height:18px;margin-right:30px}.uploader #preview .controls .slider output{position:absolute;top:-45px;padding:7px 10px;background:#76de76;color:#fff;border-radius:10px 10px 10px 0;font-weight:bold}.uploader #preview .controls .slider #fader{cursor:pointer;position:relative;left:3px;width:100%;height:10px;background:#76de76;-webkit-appearance:none;outline:none;border-radius:4px}.uploader #preview .controls .slider #fader::-webkit-slider-thumb{-webkit-appearance:none;width:25px;height:25px;border-radius:50%;background:#76de76;box-shadow:0 0 2px #222}.uploader #preview .controls>img{-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;-webkit-filter:invert(100%);filter:invert(100%);background:rgba(255,255,255,0.33);max-width:40px;border-radius:15px;border:1px solid #000;padding:7.5px}.borders{max-width:1000px;max-height:1000px;position:relative;overflow:hidden}.borders__top,.borders__bottom{height:3px;width:100%}.borders__left,.borders__right{width:3px;height:100%}.borders__top{position:absolute;-webkit-animation:fromLeftToRight 3s 0s linear infinite;animation:fromLeftToRight 3s 0s linear infinite;top:0;background:linear-gradient(to right, #ffefba, #fff)}.borders__right{position:absolute;-webkit-animation:fromTopToBottom 3s 1.5s linear infinite;animation:fromTopToBottom 3s 1.5s linear infinite;right:0;top:-101%;background:linear-gradient(to bottom, #ffefba, #fff)}.borders__bottom{position:absolute;-webkit-animation:fromRightToLeft 3s 0s linear infinite;animation:fromRightToLeft 3s 0s linear infinite;bottom:0;right:-101%;background:linear-gradient(to left, #ffefba, #fff)}.borders__left{position:absolute;-webkit-animation:fromBottomToTop 3s 1.5s linear infinite;animation:fromBottomToTop 3s 1.5s linear infinite;left:0;bottom:-101%;background:linear-gradient(to top, #ffefba, #fff)}@-webkit-keyframes fromLeftToRight{from{left:-101%}to{left:101%}}@keyframes fromLeftToRight{from{left:-101%}to{left:101%}}@-webkit-keyframes fromTopToBottom{from{top:-101%}to{top:101%}}@keyframes fromTopToBottom{from{top:-101%}to{top:101%}}@-webkit-keyframes fromRightToLeft{from{right:-101%}to{right:101%}}@keyframes fromRightToLeft{from{right:-101%}to{right:101%}}@-webkit-keyframes fromBottomToTop{from{bottom:-101%}to{bottom:101%}}@keyframes fromBottomToTop{from{bottom:-101%}to{bottom:101%}}@media screen and (max-width: 1276px){.borders{min-width:50vw;min-height:50vw}}@media screen and (max-width: 1024px){.borders{min-width:65vw;min-height:65vw}}@media screen and (max-width: 768px){.borders{min-width:80vw;min-height:80vw}}

