$duration: 3s;
$sidePercent: -101%;
$gradientFirstColor: #ffefba;
$gradientSecondColor: #ffffff;

@mixin animatedBorder($blockSide, $animationType, $delay) {
  position: absolute;
  animation: $animationType $duration $delay linear infinite;
  #{$blockSide}: 0;
  @if $blockSide == "right" {
    top: $sidePercent;
    background: linear-gradient(
      to bottom,
      $gradientFirstColor,
      $gradientSecondColor
    );
  } @else if $blockSide == "bottom" {
    right: $sidePercent;
    background: linear-gradient(
      to left,
      $gradientFirstColor,
      $gradientSecondColor
    );
  } @else if $blockSide == "left" {
    bottom: $sidePercent;
    background: linear-gradient(
      to top,
      $gradientFirstColor,
      $gradientSecondColor
    );
  } @else {
    background: linear-gradient(
      to right,
      $gradientFirstColor,
      $gradientSecondColor
    );
  }
}

@mixin borderAnimate($side) {
  from {
    #{$side}: $sidePercent;
  }
  to {
    #{$side}: -$sidePercent;
  }
}

.borders {
  max-width: 1000px;
  max-height: 1000px;
  position: relative;
  overflow: hidden;
  &__top,
  &__bottom {
    height: 3px;
    width: 100%;
  }
  &__left,
  &__right {
    width: 3px;
    height: 100%;
  }
  &__top {
    @include animatedBorder(top, fromLeftToRight, 0s);
  }
  &__right {
    @include animatedBorder(right, fromTopToBottom, $duration / 2);
  }
  &__bottom {
    @include animatedBorder(bottom, fromRightToLeft, 0s);
  }
  &__left {
    @include animatedBorder(left, fromBottomToTop, $duration / 2);
  }
  @keyframes fromLeftToRight {
    @include borderAnimate(left);
  }
  @keyframes fromTopToBottom {
    @include borderAnimate(top);
  }
  @keyframes fromRightToLeft {
    @include borderAnimate(right);
  }
  @keyframes fromBottomToTop {
    @include borderAnimate(bottom);
  }
  @media screen and (max-width: 1276px) {
    min-width: 50vw;
    min-height: 50vw;
  }
  @media screen and (max-width: 1024px) {
    min-width: 65vw;
    min-height: 65vw;
  }
  @media screen and (max-width: 768px) {
    min-width: 80vw;
    min-height: 80vw;
  }
}
